export default function ListaBoleto() {
	const classes = useStyles();
	const theme = useTheme();
	const dispatch = useDispatch();
	const history = useHistory();
	const matches = useMediaQuery(theme.breakpoints.down('sm'));
	const token = useAuth();
	const [loading, setLoading] = useState(false);
	const userData = useSelector((state) => state.userData);
	const [filters, setFilters] = useState({
		like: '',
		order: '',
		mostrar: '',
	});
	const [page, setPage] = useState(1);
	const [value, setValue] = useState(0);
	const debouncedLike = useDebounce(filters.like, 800);
	const shrink = filters.like.length > 0;

	useEffect(() => {
		dispatch(
			loadBoletoList(
				token,
				page,
				debouncedLike,
				filters.order,
				filters.mostrar
			)
		);
	}, [page, debouncedLike, filters.order, filters.mostrar]);

	const boletosList = useSelector((state) => state.boletosList);

	moment.locale();

	useEffect(() => {
		dispatch(loadUserData(token));
	}, [token]);

	useEffect(() => {
		dispatch(
			loadPagadoresFilter(
				token,
				page,
				debouncedLike,
				filters.order,
				filters.mostrar,
				undefined
			)
		);
	}, [page, filters.order, filters.mostrar, debouncedLike]);

	useEffect(() => {
		return () => {
			dispatch(setHeaderLike(''));
		};
	}, []);

	const handleChangePage = (e, value) => {
		setPage(value);
	};
	const handleChange = (event, newValue) => {
		setValue(newValue);
	};
	const handleChangeIndex = (index) => {
		setValue(index);
	};
	const getIndicatorColor = (index) =>
		index === value ? `2px solid ${APP_CONFIG.mainCollors.primary}` : null;

	const handleClickRow = (row) => {
		const path = generatePath(
			'/dashboard/adquirencia/acao/link-de-pagamento/:subsectionId',
			{
				subsectionId: row.id,
			}
		);
		history.push(path);
	};
	/* 
	useEffect(() => {
		return () => {
			setFilters({ ...filters });
		};
	}, []); */

	const columns = [
		{
			headerText: 'Criado em',
			key: 'created_at',
			CustomValue: (data) => {
				const p = data.split(/\D/g);
				const dataFormatada = [p[2], p[1], p[0]].join('/');
				return (
					<Box display="flex" justifyContent="center">
						<FontAwesomeIcon icon={faCalendarAlt} size="lg" />
						<Typography style={{ marginLeft: '6px' }}>
							{dataFormatada}
						</Typography>
					</Box>
				);
			},
		},
		{
			headerText: 'Situação',
			key: 'status',
			CustomValue: (status) => {
				return <TransactionStatusColumn status={status} />;
			},
		},
		{
			headerText: 'Pagador',
			key: 'pagador',
			CustomValue: (pagador) => {
				return (
					<Box>
						<Typography>
							{pagador.nome ? pagador.nome : pagador.razao_social}
						</Typography>
						<Typography>
							{pagador.documento ? pagador.documento : null}
						</Typography>
					</Box>
				);
			},
		},
		{
			headerText: 'Descrição',
			key: 'descricao',
			CustomValue: (descricao) => {
				return (
					<Tooltip title={descricao}>
						<Box marginLeft="12px">
							<FontAwesomeIcon icon={faInfo} />
						</Box>
					</Tooltip>
				);
			},
		},
		{
			headerText: 'Ações',
			key: 'menu',
		},
	];

	const Editar = (row) => {
		const [anchorEl, setAnchorEl] = useState(null);
		const [modalCapturar, setModalCapturar] = useState(false);
		const [captura, setCaptura] = useState('');

		const handleClick = (event) => {
			setAnchorEl(event.currentTarget);
		};
		const handleClose = () => {
			setAnchorEl(null);
		};

		const handleCaptura = async () => {
			setLoading(true);
			if (row.row.status === 'Pago') {
				toast.error('Cobrança já capturada');
				setLoading(false);
				setModalCapturar(false);
			} else {
				const resCaptura = await dispatch(
					postCapturaCobrancaAction(row.row.id, captura.valor)
				);
				if (resCaptura) {
					toast.error('Erro ao capturar');
					setLoading(false);
					setModalCapturar(false);
				} else {
					toast.success('Captura realizada com sucesso!');
					setLoading(false);
					setModalCapturar(false);
				}
			}
		};

		return (
			<>
				<Box
					onClick={handleClick}
					style={{
						backgroundColor: 'white',
						display: 'flex',
						flexDirection: 'column',
						height: '50px',
						width: '50px',
						cursor: 'pointer',
						borderRadius: '32px',
						alignItems: 'center',
						justifyContent: 'center',

						'&:hover': {
							cursor: 'pointer',
							backgroundColor: APP_CONFIG.mainCollors.primaryVariant,
						},
					}}
				>
					<SettingsIcon
						style={{
							color: APP_CONFIG.mainCollors.primary,
							fontSize: '30px',
							'&:hover': {
								backgroundColor: APP_CONFIG.mainCollors.primaryVariant,
							},
						}}
					/>
				</Box>
				<Menu
					onClick={() => {}}
					id="simple-menu"
					anchorEl={anchorEl}
					keepMounted
					open={Boolean(anchorEl)}
					onClose={handleClose}
				>
					<MenuItem
						style={{
							color: APP_CONFIG.mainCollors.secondary,
							fontFamily: 'Montserrat-Regular',
						}}
						/* onClick={() => setIsModalConfirmarPropriedadeOpen(true)} */
					>
						Cobrar
					</MenuItem>
				</Menu>
				<>
					<LoadingScreen isLoading={loading} />
					<Dialog
						onClose={() => setModalCapturar(false)}
						open={modalCapturar}
						onBackdropClick={() => setModalCapturar(false)}
					>
						<Box width="500px">
							<DialogTitle>
								<Typography
									align="center"
									variant="h6"
									style={{ color: APP_CONFIG.mainCollors.primary }}
								>
									Realizar Captura
								</Typography>
							</DialogTitle>
							<Box display="flex" flexDirection="column" padding="24px">
								<Box display="flex" flexDirection="column">
									<TextField
										disabled
										fullWidth
										InputLabelProps={{ shrink: true, color: 'grey' }}
										label="Valor da cobrança"
										value={'R$ ' + row.row.valor}
										style={{
											marginBottom: '6px',
											width: '60%',
											alignSelf: 'center',
											color: 'grey',
										}}
									/>

									<Typography
										style={{
											alignSelf: 'center',
											color: APP_CONFIG.mainCollors.primary,
										}}
									>
										Valor da Captura
									</Typography>
									<CurrencyInput
										label="Valor Mensal"
										placeHolder="R$0,00"
										className={classes.currencyInput}
										decimalSeparator=","
										thousandSeparator="."
										prefix="R$ "
										value={captura.valor}
										onChangeEvent={(event, maskedvalue, floatvalue) =>
											setCaptura({
												...captura,
												valor: floatvalue,
											})
										}
									/>

									{/* <CurrencyInput
										variant="outlined"
										className={classes.currency}
										decimalSeparator=","
										thousandSeparator="."
										prefix="R$ "
										value={captura.valor}
										onChangeEvent={(event, maskedvalue, floatvalue) =>
											setCaptura({
												...captura,
												valor: floatvalue,
											})
										}
										style={{
											marginBottom: '6px',
											width: '60%',
											alignSelf: 'center',
										}}
									/> */}

									<Box alignSelf="center" marginTop="6px">
										<CustomButton
											color="purple"
											onClick={() => handleCaptura()}
										>
											<Typography>Capturar</Typography>
										</CustomButton>
									</Box>
								</Box>
							</Box>
						</Box>
					</Dialog>
				</>
			</>
		);
	};

	return (
		<Box className={classes.root}>
			<LoadingScreen isLoading={loading} />

			<Box className={classes.main}>
				<CustomHeader pageTitle="Boleto" />

				<Box className={classes.dadosBox}>
					<Box
						style={{
							width: '100%',
							display: 'flex',
							flexDirection: 'column',
							alignItems: 'center',
						}}
					>
						<Box
							style={{
								display: 'flex',
								backgroundColor: APP_CONFIG.mainCollors.backgrounds,
								alignItems: 'center',
								borderRadius: '17px',
								flexDirection: 'column',
								/* maxWidth: '90%', */
								minWidth: '100%',

								/* alignItems: 'center', */
							}}
						>
							<Box
								style={{
									display: 'flex',
									width: '100%',
									alignItems: 'center',
									justifyContent: 'space-between',
									padding: '30px',
								}}
							>
								<TextField
									fullWidth
									value={filters.like}
									onChange={(e) =>
										setFilters({
											...filters,
											like: e.target.value,
										})
									}
									InputLabelProps={{
										shrink: shrink,
										className: shrink
											? undefined
											: classes.inputLabelNoShrink,
									}}
									variant="outlined"
									label="Buscar por nome, documento..."
									style={{ width: '70%' }}
									InputProps={{
										startAdornment: (
											<SearchIcon
												style={{
													fontSize: '30px',
													color: APP_CONFIG.mainCollors.primary,
												}}
											/>
										),
									}}
								/>
								<CustomButton
									color="purple"
									component={Link}
									to={`lista-gerar-boleto`}
								>
									<Typography
										style={{
											fontFamily: 'Montserrat-Regular',
											fontSize: '14px',
											color: 'white',
										}}
									>
										Nova cobrança
									</Typography>
								</CustomButton>{' '}
							</Box>
							<Box
								style={{
									width: '100%',

									borderRadius: 27,
									borderTopLeftRadius: 27,
									borderTopRightRadius: 27,
								}}
							>
								<Box
									display="flex"
									style={{
										marginTop: '10px',
										marginBottom: '16px',
										margin: 30,
									}}
								>
									<Box
										style={
											value === 3
												? {
														width: '100%',
														borderTopRightRadius: 27,
														borderTopLeftRadius: 27,
												  }
												: {
														width: '100%',
														borderTopRightRadius: 27,
														borderTopLeftRadius: 27,
												  }
										}
									>
										{boletosList && boletosList.per_page ? (
											<>
												<Box minWidth={!matches ? '800px' : null}>
													<CustomTable
														columns={columns ? columns : null}
														data={boletosList.data}
														Editar={Editar}
													/>
												</Box>
												<Box alignSelf="flex-end" marginTop="8px">
													<Pagination
														variant="outlined"
														color="secondary"
														size="large"
														count={boletosList.last_page}
														onChange={handleChangePage}
														page={page}
													/>
												</Box>
											</>
										) : (
											<Box>
												<LinearProgress color="secondary" />
											</Box>
										)}
									</Box>
								</Box>
							</Box>
						</Box>
					</Box>
				</Box>
			</Box>
		</Box>
	);
}
